.btn::before {
    position: absolute;
    background-color: #7317D6;
    content: "";
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
    z-index: -1;
    transition: all 400ms ease;
    height: 0%;
