* {
  margin: 0;
  padding: 0;
}

html, body {
  font-family: sans-serif;
  background-image: url(https://images.pexels.com/photos/669616/pexels-photo-669616.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-position: center;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.425);
  background-blend-mode: multiply;
}

.main_wrapper {
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: 1fr 1.5fr;
  grid-template-rows: auto;
  grid-template-areas: 
    "header header"
    "sidemenu datatable"
    "footer footer";
}

.header {
  grid-area: header;
}

.sidemenu {
  grid-area: sidemenu;
}

.datatable {
  grid-area: datatable;
}
.footer {
  grid-area: footer;
}

